.advantages {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.advantages__item {
  background-color: @primary;
  width: 100%;
  text-align: center;

  img {
    vertical-align: bottom;
    height: 100%;
    object-fit: cover;
    width: 100%;
    // Object-fit polyfill
    // stylelint-disable-next-line
    font-family: "object-fit: cover";
  }
}

.advantages__item--real-town {
  position: relative;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    background-image: url("../img/bg-triangle.svg");
    background-position-x: center;
    background-position-y: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 34px;
    z-index: 10;
  }

  .advantages__content {
    margin-top: 75px;
    margin-bottom: 41px;
  }
}

.advantages__item--short {
  position: relative;
  background-color: @alt;

  &::after {
    content: "";
    position: absolute;
    height: 2px;
    bottom: 0;
    left: 20px;
    right: 20px;
    background-color: rgba(0, 0, 0, 0.1);
  }

  &:last-child::after {
    display: none;
  }

  .advantages__content {
    margin-top: 26px;
    margin-bottom: 30px;
  }

  .advantages__slogan {
    color: @text-dark;
  }

  .advantages__description {
    margin-left: 32px;
    margin-right: 32px;
  }

  .advantages__description,
  .advantages__number {
    color: @text;
  }
}

.advantages__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
  margin: 39px 0;

  br {
    display: none;
  }
}

.advantages__slogan {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  color: #ffffff;
}

.advantages__description {
  font-size: 14px;
  line-height: 1.5;
  color: #ffffff;
  text-transform: uppercase;
  margin: 18px 25px 0 27px;
}

.advantages__number {
  display: block;
  color: #ffffff;
  font-size: 14px;
  margin-top: 12px;
}

.advantages__image {
  flex: 1;
}

@media (min-width: @tablet-width) {
  .advantages {
    margin-top: 53px;
  }

  .advantages__item {
    display: flex;
    width: 100%;
    height: 256px;
    overflow: hidden;
  }

  .advantages__item:nth-child(6) .advantages__description {
    margin: 25px 55px 0 55px;
  }

  .advantages__item--real-town {
    &::before {
      display: none;
    }

    .advantages__content {
      margin: 45px 0;
    }
  }

  .advantages__item--bridge-devil .advantages__content {
    order: 2;
  }

  .advantages__item--short {
    width: 33.33%;
    text-align: center;

    &::after {
      display: none;
    }

    .advantages__content {
      width: 100%;
      margin: 45px 0;
    }

    .advantages__slogan {
      margin: 0 50px 0;
    }

    .advantages__number {
      margin-top: 11px;
    }

    .advantages__description {
      margin: 25px 40px 0 40px;
    }
  }

  .advantages__content {
    width: 33.3%;

    br {
      display: block;
    }
  }

  .advantages__slogan {
    margin: 0 50px 0;
    font-size: 21px;
    line-height: 1.26;
  }

  .advantages__description {
    margin: 23px 44px 0 44px;
  }

  .advantages__number {
    margin-top: 11px;
  }
}

@media (min-width: @desktop-width) {
  .advantages__content {
    margin: 55px 0;
  }

  .advantages__slogan {
    margin: 0 100px 13px 100px;
  }

  .advantages__number {
    margin: 0 40px 22px 40px;
  }

  .advantages__description {
    margin: 0 60px 0 60px;
  }

  .advantages__item--real-town .advantages__content {
    margin: 38px 0 45px 0;
  }

  .advantages__item--bridge-devil {
    .advantages__content {
      margin: 41px 0 45px 0;
    }

    .advantages__number {
      margin: 0 40px 24px 40px;
    }

    .advantages__description {
      margin: 0 90px 0 90px;
    }
  }

  .advantages__item--short {
    .advantages__slogan {
      margin: 0 120px 0 120px;
    }

    .advantages__number {
      margin: 11px 40px 0 40px;
    }

    &:nth-child(6) .advantages__description,
    .advantages__description {
      margin: 25px 40px 20px 40px;
    }
  }
}
