.video__preview {
  vertical-align: bottom;
  object-fit: cover;
  object-position: top center;
  font-family: "object-fit: cover";
  height: 201px;
  width: 100%;
}
.video__wrapper {
  min-height: 201px;
  margin-bottom: 90px;
  position: relative;
}
.video__actions {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px;
  position: absolute;
  width: 100%;
  bottom: -68px;
}
.video__volume {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.video__replay {
  flex: 1;
}
.video__icon-button {
  border: 0;
  background-color: transparent;
  padding: 10px;
  cursor: pointer;
}
.video__icon-button svg {
  fill: #5c6870;
}
.video__icon-button:hover svg {
  fill: #333333;
}
.video__icon-button:active svg,
.video__icon-button:focus svg {
  fill: rgba(51, 51, 51, 0.3);
}
.video__replay,
.video__subtitles,
.video__fullscreen {
  background-color: #f2f2f2;
}
.video__replay,
.video__fullscreen {
  background-color: #f2f2f2;
