html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "PTSans", "Arial", sans-serif;
  background-color: @alt;
  font-size: 16px;
  min-width: 320px;
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  border: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-js {
  .main-nav__list {
    display: flex;
    flex-direction: column;
  }

  .main-nav__toggle {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .no-js {
    .main-nav__list {
      flex-direction: row;
    }
  }
}
